<template>
    <p-fieldset>
        <template #legend> Current Patient: {{ currentPatient }} </template>
        <div class="p-fluid p-formgrid p-grid p-input-filled">
            <div class="p-field p-col-12 p-md-4">
                <label for="patient-search">Patient Search</label>
                <p-auto-complete
                    v-model="selectedPaitent"
                    :suggestions="filteredPatients"
                    @complete="searchPateent($event)"
                    field="name"
                />
            </div>
            <div class="p-field p-col-12 p-md-2">
                <label>-</label>
                <p-input-text type="text" />
            </div>
            <div class="p-field p-col-12 p-md-1">
                <label>-</label>
                <br />
                <p-button icon="pi pi-search" />
            </div>
            <div class="p-col-12 p-md-4"></div>
            <div class="p-field p-col-12 p-md-1">
                <label>-</label>
                <br />
                <p-button label="Reset Password" />
            </div>
            <div class="p-field p-col-12 p-md-4">
                <label>Subject</label>
                <p-input-text />
            </div>
            <div class="p-col-12 p-md-8"></div>
            <div class="p-field p-col-12 p-md-6">
                <label>Message</label>
                <p-textarea rows="5" />
            </div>
        </div>
        <p-button label="send" class="p-mr-2"/>
        <p-button label="Print Message History"/>

        <h4>Current Messge History</h4>
        <p-data-table
            :value="messageHistories"
            responsiveLayout="scroll"
            showGridlines
            class="p-datatable-sm p-mb-1"
        >
            <p-column header="Date" field="date"/>
            <p-column header="Subject" field="subject"/>
            <p-column header="Read" field="read"/>
            <p-column header="To" field="to"/>
            <p-column header="From" field="from"/>
            <p-column header="Message" field="message"/>
        </p-data-table>
    </p-fieldset>
</template>

<script>
import { computed, ref } from "vue";
export default {
    setup() {
        const patients = ref([{ name: "Aberdine" }]);
        const currentPatient = ref("Thamlingam Tarhami");
        const selectedPaitent = ref();
        const filteredPatients = ref([]);
        const messageHistories = ref([
            {date: '2021-01-10', message: 'weocome to newlife'}
        ])

        const searchPateent = (event) => {
            if (!event.query.trim().length) {
                filteredPatients.value = [...patients.value];
            } else {
                filteredPatients.value = patients.value.filter((patient) => {
                    return patient.name
                        .toLowerCase()
                        .startsWith(event.query.toLowerCase());
                });
            }
        };

        return {
            currentPatient,
            selectedPaitent,
            filteredPatients,
            searchPateent,
            messageHistories
        };
    },
};
</script>